@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.btn-round {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    outline: none;
    border: none;
    cursor: pointer;
    background-color: rgb(0, 188, 212);
    -webkit-transition: all .2s;
    transition: all .2s;
}

.btn-round:hover {
    background-color: rgb(62, 219, 240);
}

.edit__btn {
    margin-right: 20px;
}

.icon {
    width: 20px;
    height: 20px;
}

.card-title {
    font-size: 5px;
}
.subtext {
    font-size: 12px;
}

@media (max-width: 980px) {
    .subtext {
        font-size: 20px;
    }
}
.formErrors {
    color: red;
}
.form-controll {
    width: 80%;
}
.form-controll:not(:last-child) {
    margin-bottom: 30px;
}
.input{
    color: #fff !important;
    font-size: 26px;
}
.label {
    color: #fff !important;
    font-size: 20px;
}
.search-box {
    display: flex;
    justify-content: center;
    margin: 2rem;
    padding: 2rem;
    width: 100%;
}

.search-input {
    width: 100%;
    height: 3rem;
    padding-left: 2rem;
    margin-right: -3.25rem;
    border-radius: 25px;
    outline: none;
    border: 1px solid #aca5a5;
    color: inherit;
    font-family: inherit;
    -webkit-transition: all .3s;
    transition: all .3s;
}

.search-input:focus {
    width: 80%;
    /* outline: none; */
    /* border: none; */
}

.search__icon {
    width: 2rem;
    height: 2rem;
    opacity: .7;
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    -webkit-transition: all .2s;
    transition: all .2s;
}

.search__icon:hover {
    opacity: 1;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
}

.search__icon:active {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
}

.row {
    margin-top: 30px
}
